/* ScoreDisplay.css */
.ScoreDisplay {
    display: flex;
    text-align: center;
    width: 100%;
    height: 100%;
    justify-content: center;
  }
  
  .ScoreDisplay span {
    margin: 0;
    font-size: 150px;
    font-weight: bold;
    color: rgba(255, 255, 255, 0.8);
    z-index: 1;
  }
  